.bullet-list {
    &__name {
        @extend %h3;
        margin-bottom: 13px;
    }
    &__item { 
        @extend %font-body;
        color: $dark75;
        line-height: 24px;
        margin-bottom: 10px;
        &:before {
            content: '';
            display: inline-block;
            width: 10.43px;
            height: 10px;
            background: $dark25;
            border-radius: 5px;
            margin-right: 11px;
        } 
    }
}